.cell {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 10vmin;
    font-weight: bold;
    color: var(--accent-color);
    background: var(--content-background-color);
    border: var(--content-background-color);
    border-radius: var(--default-border-radius);
    outline: none;
    /* box-shadow: var(--box-shadow); */
  }
  
  .cell:active, .cell:hover, .cell.winner {
    cursor: pointer;
    color: var(--content-background-color);
    background: var(--primary-brighter);
  }
  
  .cell-content {
    overflow: hidden;
    width: 0;
    height: 0;
    transition: width var(--selection-animation-duration), height var(--selection-animation-duration);
  }
  
  .cell-content.populated {
    width: 12vmin;
    height: 12vmin;
  }